import Vue from 'vue'
import { Notify } from 'vant'
// 全局注册
Vue.use(Notify)

class ValidForm {
    valid(o) {
        for (let key in o) {
            if (!o[key].reg.test(o[key].value)) {
                Notify({
                    message: o[key].errorMsg,
                    color: '#fff',
                    background: '#3585f9',
                });
                return false
            }
        }
        return true
    }
}
export const validForm = new ValidForm();