<template>
  <div class="my">
    <div v-if="Object.keys(myData).length>0">
      <div class="my-box" :style="{backgroundImage:`url(${myData.userBg})`}">
        <van-uploader :after-read="uploadBg" />
        <div class="navName">我的</div>
        <div class="my-top">
          <div class="top-content">
            <div class="left-img">
              <img :src="myData.userImg" alt class="auto-img" />
            </div>
            <div class="right-name">
              <div class="name">{{myData.nickName}}</div>
            </div>
            <div class="desc">简介:{{myData.desc==''?'这家伙很懒，什么也没留下':myData.desc}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="my-box" v-else>
      <div class="btn">
        <van-button round type="info" @click="goLogin">请登录</van-button>
      </div>
    </div>
    <div class="mes-box">
      <div class="mes-boxing">
        <van-cell
          :title="item.title"
          size="large"
          is-link
          v-for="(item,index) in mynavData"
          :key="index"
          :to="item.to"
          :icon="item.icon"
        />
      </div>
      <div class="mes-boxing">
        <div class="manager-box">
          <van-cell title="修改密码" is-link @click="showPopup" icon="edit" />
          <van-cell title="注销账号" is-link icon="warning-o" @click="cancellation" />
        </div>

        <van-popup v-model="show" closeable round position="bottom" :style="{ height: '40%' }">
          <div class="regist-content">
            <div class="regist-title">
              <h2>修改密码</h2>
            </div>
            <div class="regist-mes">
              <van-form>
                <van-field
                  v-for="(item,index) in passwords"
                  :key="index"
                  :type="item.isPassword ? 'password':'text'"
                  :label="item.title"
                  placeholder="请输入6-16位且为字母开头的密码"
                  :right-icon="item.isPassword ? 'closed-eye':'eye-o'"
                  @click-right-icon="showPassword(item)"
                  v-model="item.password"
                />
                <div class="regist-btn2">
                  <van-button
                    round
                    block
                    type="info"
                    native-type="submit"
                    @click="modifyPassword"
                  >确认修改</van-button>
                </div>
              </van-form>
            </div>
          </div>
        </van-popup>
      </div>
      <div class="signout-box">
        <van-button block round @click="signOut">退出登录</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import "../mainViews/less/my.less";
import { utils } from "../../assets/js/utils";
import { validForm } from "../../assets/js/validForm";
export default {
  data() {
    return {
      myData: [],
      mynavData: [
        {
          title: "个人资料",
          icon: "user-circle-o",
          to: { name: "PersonalData" },
        },
        { title: "我的收藏", icon: "like-o", to: { name: "Collection" } },
        {
          title: "地址管理",
          icon: "location-o",
          to: { name: "AddressManagement" },
        },
      ],
      passwords: {
        old: {
          password: "",
          title: "旧密码",
          isPassword: true,
        },
        new: {
          password: "",
          title: "新密码",
          isPassword: true,
        },
      },
      active: 0,
      show: false,
      maxSize: 500*1024,
    };
  },
  created() {
    this.getMy();
  },
  methods: {
    goLogin() {
      this.$router.push({ name: "Login" });
    },
    //获取个人信息
    getMy() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString: localStorage.getItem("WTPTK"),
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "A001") {
            this.myData = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //上传背景图片
    uploadBg(file) {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#0c34ba",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      //获取文件类型
      let type = file.file.type.split("/")[1];
      let imgType = ["jpg", "png", "gif", "jpeg"]; //控制图片的类型
      if (imgType.indexOf(type) == -1) {
        this.$toast("仅支持" + imgType.join(","));
        return;
      }
      //控制图片的大小
      if (this.maxSize < file.file.size) {
        this.$toast("上传图片大小不能超过" + this.maxSize + "KB");
        return;
      }
      //base64码
      let base64Img = file.content.replace(/data:image\/[A-Za-z]+;base64,/, "");

      let data = {
        appkey: this.appkey,
        tokenString: tokenString,
        imgType: type,
        serverBase64Img: base64Img,
      };
      data = utils.queryString(data);
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == "I001") {
            this.myData.userBg = result.data.userBg;
          }
          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    back() {
      this.$router.go(-1);
    },
    showPopup() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.show = true;
    },
    //显示密码
    showPassword(item) {
      item.isPassword = !item.isPassword;
    },
    //修改密码
    modifyPassword() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }

      let pas = {
        oldPassword: {
          value: this.passwords.old.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "旧密码错误",
        },
        password: {
          value: this.passwords.new.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "新密码格式错误",
        },
      };
      //验证
      if (!validForm.valid(pas)) {
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let data = {
        appkey: this.appkey,
        tokenString,
        password: this.passwords.new.password,
        oldPassword: this.passwords.old.password,
      };
      data = utils.queryString(data);
      this.axios({
        method: "POST",
        url: "/updatePassword",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == "E001") {
            this.$toast(result.data.msg);
            localStorage.removeItem("WTPTK");
            this.$router.push({ name: "Login" });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //退出登录
    signOut() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
        return;
      }
      this.$dialog
        .confirm({
          title: "退出登录",
          message: "是否退出登录?",
          confirmButtonColor: "#3585f9",
        })
        .then(() => {
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });
          localStorage.removeItem("WTPTK");
          setTimeout(() => {
            this.$toast.clear();
            this.$router.push({ name: "Login" });
          }, 800);
        })
        .catch(() => {
          // on cancel
        });
    },
    //注销账号
    cancellation() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
        return;
      }
      this.$dialog
        .confirm({
          title: "注销账号",
          message: "是否注销账号?",
          confirmButtonColor: "#3585f9",
        })
        .then(() => {
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          //参数序列化
          let data = utils.queryString({
            appkey: this.appkey,
            tokenString,
          });

          this.axios({
            method: "POST",
            url: "/destroyAccount",
            data,
          })
            .then((result) => {
              this.$toast.clear();
              this.$toast(result.data.msg);
              if (result.data.code == "G001") {
                localStorage.removeItem("WTPTK");
                setTimeout(() => {
                  this.$router.push({ name: "Login" });
                }, 800);
              }
            })
            .catch((err) => {
              this.$toast.clear();
            });
        })
        .catch(() => {
          // on cancel
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>